import ListModel from "./models/List";

const MODELS_MAP = {
  list: ListModel
};

let modelInstance = new Map();

export default {
  beforeMount: (el, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    const type = binding.arg || "list";
    if(!modelInstance.has(id))
      modelInstance.set(id,[new MODELS_MAP[type](el)]);
    else { 
      const instanceArr = modelInstance.get(id);
      modelInstance.set(id, [...instanceArr ,new MODELS_MAP[type](el)]);
    }
  },
  updated:(el, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    const instanceArr = modelInstance.get(id);
    instanceArr?.forEach((instance) => instance?.initEvents());
  },
  unmounted: (el, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    if(modelInstance.has(id)) {
      const instanceArr = modelInstance.get(id);
      instanceArr?.forEach((instance) => instance?.removeEvents());
      modelInstance.delete(id);
    }
  }
};